body {
    * {
        font-family: sans-serif, 'Roboto Slab';
        box-sizing: border-box; } }

input {
    outline: none; }

textarea {
    resize: none; }

.flex {
    display: flex; }

.h-100 {
    height: 100%; }

img {
    max-width: 100%; }

html,
body {
    height: 100%; }

.header {
    position: relative;
    background-image: url('../images/bg.jpg');
    background-size: cover;
    background-position: 50%;
    min-height: 754px;

    &::after {
        position: absolute;
        content: '';
        top: 0;
        right: 0;
        left: 0;
        bottom: 0;
        background-color: rgba(0,0,0,.4);
        z-index: 1; } }

.header-top {
    position: relative;
    padding-top: 20px;
    z-index: 10; }

.logo {
    font-size: 40px;
    font-weight: 900;
    line-height: 1.5;
    color: #ffffff;
    letter-spacing: 2px; }

.age {
    position: relative;
    display: inline-block;
    color: #ffffff;
    font-size: 18px;
    padding: 10px 7px;
    margin-right: 10px;

    &::after {
        position: absolute;
        content: '';
        top: 0;
        right: 0;
        left: 0;
        bottom: 0;
        width: 40px;
        height: 40px;
        border: 1px solid #ffffff;
        border-radius: 50%; } }

.header-social {
    margin-bottom: 20px;

    a {
        display: inline-block;
        position: relative;
        color: #ffffff;
        font-size: 20px;
        padding: 10px 0;
        border: 1px solid #ffffff;
        margin-left: 18px;
        width: 40px;
        height: 40px;
        text-align: center; } }

.header-middle {
    position: relative;
    z-index: 10;

    p {
        color: #ffffff;
        margin-bottom: 20px; }

    i {
        color: gold;
        display: inline-block;
        margin-right: 15px; } }

h1 {
    font-size: 66px;
    font-weight: 900;
    color: #ffffff;
    letter-spacing: 2px;
    padding: 20px  0; }

.btn {
    min-width: 230px;
    text-align: center;
    padding: 5px 10px;
    background-color: transparent;
    margin: 15px 0;
    cursor: pointer;
    font-weight: 700; }

.btn-white {
    color: #ffffff;
    border: 1px solid #ffffff; }

.btn-small {
    transform: scale(.8); }


.phone-bottom {
    position: relative;
    z-index: 10;

    width: 69%;
    margin: 85px auto 0;
    padding-left: 10px;

    h2 {
        font-size: 32px;
        font-weight: 900;
        color: #ffffff;
        margin-bottom: 20px; }

    p {
        color: #ffffff;
        font-size: 19px;
        font-weight: 900; } }

.slider {
    width: 100%;
    position: relative; }

.slick-track {
    display: flex;
    align-items: center;
    width: 100%; }

.phone-img {
    position: absolute;
    left: 62%;
    top: -250px;
    transform: translateX(-50%);
    background-image: url('../images/phone.png');
    width: 350px;
    height: 689px;
    background-size: cover;
    background-position: 50%;
    z-index: 10; }

.icon-down {
    position: absolute;
    right: -70px;
    color: #ffffff;
    font-size: 30px;
    top: 0;
    animation: icon-down 1s 0s infinite; }

@keyframes icon-down {
    0% {
        transform: translateY(0px); }
    50% {
        transform: translateY(10px); }
    100% {
        transform: translateY(0px); } }

.carousel {
    height: 450px;
    margin-top: 40px; }

.slick-list {
    width: 100%; }

.carousel-item {
    background-size: contain;
    height: 250px;
    background-position: 50%;
    background-repeat: no-repeat;
    position: relative;

    h3 {
        color: #ffffff;
        font-size: 18px;
        padding: 15px 15px 15px 30px;
        background-color: rgba(0,0,0,.5); }

    &.slick-center {
        position: relative;
        height: 350px;
        transform: scale(1.07);
        z-index: 12;

        h3 {
            padding: 30px 15px 15px 15px; }

        .carousel-item--btn {
            opacity: 1; } } }

.carousel-item--btn {
    position: absolute;
    bottom: 15px;
    right: 15px;
    min-width: 200px;
    opacity: 0; }

.slick-custom-arrow {
    position: absolute;
    font-size: 30px;
    color: #000000;
    top: 67%;
    padding: 20px;
    background-color: #fff;
    cursor: pointer;
    transition: .3s;

    &:hover {
        color: #716d5b;
        transition: .3s; }

    &.prev {
        right: 50px; }

    &.next {
        right: 0; } }

.grid-item {
    border-radius: 5px;
    float: left;
    width: 100px;
    height: 100px;
    margin: 0 10px 10px 0;
    border-radius: 10px;
    padding: 15px 10px;
    // background-size: cover
    background-position: 50%;
    cursor: pointer;
    background-repeat: no-repeat;

    &.height-50 {
        height: 400px; }
    &.height-25 {
        height: 200px; }
    &.width-25 {
        width: calc(25% - 10px); }
    &.width-50 {
        width: calc(50% - 10px); }
    &.height-cut {
        height: 195px; }

    &:not(.scale) {
        border: 11px solid #df1238;
        -o-border-image: linear-gradient(-30deg, #df1238, #ff6a3f) 30;
        border-image: linear-gradient(-30deg, #df1238, #ff6a3f) 30; }


    &.grid-item--large {
        width: 50%;
        height: 400px;
        overflow: hidden; }

    p {
        font-size: 14px;
        line-height: 1.1;
        max-height: 154px;
        overflow: hidden; } }


@media screen and (max-width: 1200px) {;
.phone-img {
    left: 62.3%;
    width: 322px;
    height: 639px;
    top: -231px; }

.carousel-item.slick-center {
    transform: scale(1.17); }

.carousel-item--btn {
    bottom: 37px; }

.carousel-item h3 {
    padding: 15px; }

.carousel-item.slick-center h3 {
    padding-top: 44px }; }


@media screen and (max-width: 992px) {;
h1 {
    font-size: 54px; }

.phone-img {
    left: 50%;
    width: 322px;
    height: 636px  }; }

@media screen and (max-width: 768px) {;
h1 {
    font-size: 44px; }

.phone-img {
    left: 73%;
    top: -230px; }

.icon-down {
    left: -70px;
    right: initial; }

.carousel-item--btn {
    right: 37px; }

.grid-item {

    overflow: hidden;

    p {
        max-height: 107px; }

    &.height-50 {
        height: 300px; }
    &.height-25 {
        height: 150px; }
    &.width-25 {
        width: calc(30% - 10px); }
    &.width-50 {
        width: calc(40% - 10px); }
    &.height-cut {
        height: 145px }; } }

@media screen and (max-width: 576px) {;
.header {
    min-height: 579px;
    padding: 0 15px; }

h1 {
    font-size: 24px; }

.carousel {
    height: 290px; }

.carousel-item.slick-center {
    transform: scale(1);

    h3 {
        padding: 15px; } }

.carousel-item--btn {
    right: 15px;
    bottom: 15px;
    opacity: 1; }

.grid-item {

    &.height-50 {
        height: 200px; }
    &.height-25 {
        height: 100px; }
    &.width-25 {
        width: calc(40% - 10px); }
    &.width-50 {
        width: calc(60% - 10px); }
    &.height-cut {
        height: 95px; }

    &.grid-item--large {
        width: 100%;
        height: 250px; }

    p {
        max-height: 47px; } }

.phone-img {
    display: none }; }










